// USER VARIABLES SECTION

// body styles
$accent: orange; // Accent Color selected text - виділений текст
$systemfont: -apple-system, BlinkMacSystemFont, Arial, sans-serif; // System Font Family
// Body variables
$userfont: 'Roboto', sans-serif; // User Font Family
$fontsize: 16px; // Body Font Size
$lineheight: 130%; // Document Line Height
$textcolor: #333; // Text Color

// Bootstrap compatible gutter variable => $gutter
$grid-gutter-width: 30px;
$gutter: $grid-gutter-width;

// IMPORT SECTION

// Bootstrap Reboot collection
@import 'node_modules/bootstrap/scss/bootstrap-reboot.scss';
// Bootstrap Breakpoints mixin
@import 'node_modules/bootstrap/scss/mixins/_breakpoints.scss';
// Optional Bootstrap Grid
@import 'node_modules/bootstrap/scss/bootstrap-grid.scss';
// Optional Bootstrap Utilites
@import 'node_modules/bootstrap/scss/utilities/_sizing.scss';
// Bootstrap navmenu
@import 'node_modules/bootstrap/scss/_nav.scss';
@import 'node_modules/bootstrap/scss/_navbar.scss';
@import 'node_modules/bootstrap/scss/_buttons.scss';
@import 'node_modules/bootstrap/scss/_button-group.scss';
@import 'node_modules/bootstrap/scss/_forms.scss';
@import 'node_modules/bootstrap/scss/_dropdown.scss';
@import 'node_modules/bootstrap/scss/_modal.scss';
@import 'node_modules/bootstrap/scss/_utilities.scss';

// slick carousel
