// FONTS LOAD SECTION

@mixin font($fname, $fstyle, $fweight, $furl) {
	@font-face {
		font-family: $fname;
		font-style: $fstyle;
		font-weight: $fweight;
		src: url($furl) format('truetype'); } }

@include font('Roboto', normal, 100, '../fonts/Roboto-Thin.ttf');
@include font('Roboto', normal, 300, '../fonts/Roboto-Light.ttf');
@include font('Roboto', normal, 400, '../fonts/Roboto-Regular.ttf');
@include font('Roboto', normal, 500, '../fonts/Roboto-Medium.ttf');
@include font('Roboto', normal, 700, '../fonts/Roboto-Bold.ttf');
@include font('Roboto', normal, 900, '../fonts/Roboto-Black.ttf'); // delete


// GENERAL DOCUMENT STYLES

::placeholder {
	color: $light-grey;
	@extend %regular-text-1;
	padding-left: 1%; }


::selection {
	background-color: $accent;
	color: #fff; }

input, textarea {
	outline: none;
	&:focus:required:invalid {
		border-color: red; }
	&:required:valid {
		border-color: green; } }

body {
	font-family: $userfont;
	font-size: $fontsize;
	line-height: $lineheight;
	color: $textcolor;
	min-width: 768px;
	position: relative;
	overflow-x: hidden;
	padding-left: $aside-width;
	padding-top: $headHeight;
	background-color: $dark-background-1;
	margin-top: 1px;
	margin-left: 1px; }

::-webkit-scrollbar {
	width: 0; }
